@import "./common.sass";

* {
  padding: 0;
  margin: 0; }

body {
  background: rgba(255,255,255,1);

  #root .container {
    min-height: 100vh;
    height: 100%;
    width: 80%;

    & h1, h2, h3, h4, h5, h6 {
      font-family: $primary-font; }

    & .site-link {
      color: $link-color; }

    & .content {
      font-size: 1.3rem; } } }
