@font-face {
  font-family: 'Raleway';
  src: url('/assets/fonts/Raleway-SemiBold.ttf') format('truetype'); }

@font-face {
  font-family: 'Oswald';
  src: url('/assets/fonts/Oswald-Bold.ttf') format('truetype'); }

$default-background: "/assets/image/3rd_background.jpg";
$primary-font: 'Oswald', sans-serif;
$secondary-font: 'Raleway', sans-serif;
$navbar-height: 50px;
$link-color: rgba(170, 64, 0, 1);

/**
 * Animations
 */
.tracking-in-expand {
  -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
  animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both; }

@-webkit-keyframes tracking-in-expand {
  0% {}
  letter-spacing: -0.5em;
  opacity: 0;
  40% {}
  opacity: 0.6;
  100% {}
  opacity: 1; }

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0; }
  40% {
    opacity: 0.6; }
  100% {
    opacity: 1; } }

.text-focus-in {
  -webkit-animation: text-focus-in 300ms cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  animation: text-focus-in 300ms cubic-bezier(0.550, 0.085, 0.680, 0.530) both; }
@-webkit-keyframes text-focus-in {
  0% {}
  -webkit-filter: blur(12px);
  filter: blur(12px);
  opacity: 0;

  100% {}
  -webkit-filter: blur(0px);
  filter: blur(0px);
  opacity: 1; }
@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0; }
  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1; } }
